import styled from "styled-components";
import React from 'react';

const Base = styled.div`
    overflow: auto;
    width: 100%;
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    box-sizing: border-box;
    position: relative;
`;

const CourseCarousel = ({children}) => {
    return (
        <Base>
            {children}
        </Base>
    )
}

export default CourseCarousel;