import React from 'react';
import {useParams} from 'react-router-dom';
import {} from "../../Providers/CourseProvider";
import Section from "../../Atomics/Section";
import CourseCard from "../../Atomics/CourseCard";
import CourseCarousel from "../../Atomics/CourseCarousel";
import {useGetData} from "../../Providers/CourseProvider";

const ModuleGroup = ({courseId, moduleGroupId}) => {
    const modules = useGetData('Modules', moduleGroupId);

    const constructModules = () => {
        const temp = [];

        for(let key in modules) {
            const {title, description, symbol, id} = modules[key];

            temp.push(
                <CourseCard key={key ?? 0} title={title} symbol={symbol} to={`/course/${courseId}/${moduleGroupId}/${id}`}>
                    {description}
                </CourseCard>
            );
        }

        return temp;
    };

    return constructModules();
};

const Course = () => {
    const {courseId} = useParams();
    const moduleGroups = useGetData('ModuleGroups', courseId);

    const constructModuleGroups = () => {
        const temp = [];

        for(let key in moduleGroups) {
            const item = moduleGroups[key];
            temp.push(
                <Section key={item.id ?? 0} title={item.title}>
                    <CourseCarousel style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'center'}}>
                        <ModuleGroup moduleGroupId={item.id} courseId={courseId} />
                    </CourseCarousel>
                </Section>
            )
        };

        return temp;
    };

    return constructModuleGroups();
};

export default Course;