import styled from "styled-components";
import Icon from "./Icon";
import {injectPlaceholder} from "./Placeholder";

const CardSymbol = styled(Icon)`
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: #0d47a1;
    padding: 16px;
    font-size: 48px;
    
    ${injectPlaceholder};
`;

export default CardSymbol;