import styled from "styled-components";
import React from "react";
import CardSymbol from "./CardSymbol";
import NavButton from "./NavButton";
import {injectPlaceholder} from "./Placeholder";
import CardTitle from "./CardTitle";

const Card = styled.div`
    width: 200px;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
    border-radius: 4px;
    overflow: hidden;
    flex-grow: 0;
    flex-shrink: 0;
`;

const CardDescription = styled.div`
    padding: 0 8px 8px 8px;
    font-size: 11pt;
    ${injectPlaceholder};
`;

const CardLink = styled(NavButton)`
    font-size: 10pt;
    padding: 8px;
    color: #0d47a1;
`;

const CardWrapper = styled.div`
    padding: 8px;
`;

const CourseCard = ({symbol, children, to, title, slim, ...props}) => {
    return (
        <CardWrapper>
            <Card slim={slim}>
                {slim ? null : <CardSymbol>{symbol}</CardSymbol>}
                <CardTitle>{title}</CardTitle>
                {slim ? null : <CardDescription>{children}</CardDescription>}
                <CardLink text to={to}>Zur Aufgabe</CardLink>
            </Card>
        </CardWrapper>
    )
}

export default CourseCard;